.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container img {
  animation: moveUp 1s ease-in-out;
  border-radius: 2rem;
}

@keyframes moveUp {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0) rotate(360deg);
  }
}
